<template>
	<div id="store-getcash">
		<public-header title="提现申请"></public-header>
		<div class="content">
			<div class="wrap-search">
				<div class="tab-group flex-lr border-1px-b">
					<div class="tab-item" :class="{active: filter.items.status == 0}" @click="onToggleStatus(0)">全部</div>
					<div class="tab-item" :class="{active: filter.items.status == 2}" @click="onToggleStatus(2)">申请中</div>
					<div class="tab-item" :class="{active: filter.items.status == 1}" @click="onToggleStatus(1)">提现成功</div>
					<div class="tab-item" :class="{active: filter.items.status == 3}" @click="onToggleStatus(3)">撤销</div>
				</div>
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
				<van-list
						v-model="records.loading"
						:finished="records.finished"
						@load="onLoad"
						:offset="100"
						:immediate-check="false"
						v-if="!records.empty"
						class="getcash-list"
				>
					<div class="getcash-item border-1px-b" v-for="(item, index) in records.data" :key="item.id">
						<router-link tag="div" :to="util.getUrl({path: '/pages/finance/getcashRecordDetail', query:{id: item.id}})" class="padding-10">
							<div class="font-14 padding-10-b flex-lr">
								<div class="font-bold c-default">{{item.status_cn}}</div>
								<div class="font-bold" :class="{'c-primary': item.status == 1, 'c-danger': item.status != 1}">{{item.get_fee}}</div>
							</div>
							<div class="font-14 flex-lr">
								<div class="c-gray">{{item.addtime}}</div>
								<div class="c-gray">手续费：￥{{item.take_fee}}</div>
							</div>
						</router-link>
					</div>
					<div class="loaded" v-if="records.finished">
						<div class="loaded-tips">没有更多了</div>
					</div>
				</van-list>
				<div class="no-data" v-else>
					<img src="static/img/order_no_con.png" alt="">
					<p>没有符合条件的数据!</p>
				</div>
			</van-pull-refresh>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getRecords} from "../../controller/funcCommon";
	export default {
		data() {
			return {
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				isRefresh: false,
				filter: {
					items: {
						status: '0'
					}
				},
				showPreLoading: true
			}
		},
		components: {
			publicHeader
		},
		methods: {
			onLoad(force){
				getRecords({
					vue: this,
					force: force,
					url: 'manage/finance/getcashRecord/list',
					recordsName: 'record'
				});
			},

			onPullDownRefresh() {
				this.onLoad(true);
			},

			onToggleStatus(value){
				value = parseInt(value);
				if(this.filter.items.status == value) {
					return;
				}
				this.filter.items.status = value;
			}
		},
		mounted() {
			this.onLoad();
		},
		watch: {
			filter: {
				handler(newValue, oldValue) {
					this.onLoad(true)
				},
				deep: true
			}
		}
	}
</script>

<style>
	#store-getcash .content .getcash-item{
		background-color: #fff;
	}
	#store-getcash .content .getcash-item:first-child{
		border-top: 1px solid #e0e0e0;
	}
</style>


